body {
  background-color: #f3f3f3 !important;
}

.card-me {
  border-radius: 10px;
  background-color: #f3f3f3;
  box-shadow: -3px -3px 4px #ffffff, 2px 2px 2px rgb(80, 80, 80);
}

.width {
  width: 50%;
}

@media only screen and (max-width: 800px) {
  .width {
    width: 100%;
    word-wrap: break-word;
  }
}
